// Host element styles
:host {
  display: block;
  width: 100%;
  height: 100%;

  // Deep styles for PrimeNG components
  ::ng-deep {
    // Main datatable styles
    .p-datatable {
      width: 100%;

      &-wrapper {
        overflow-x: auto;
      }

      // Table header styles
      &-thead {
        tr:nth-child(2) th {
          @apply overflow-hidden;

          .filter-wrapper {
            @apply w-full max-w-full overflow-hidden;
          }
        }
      }
    }

    // Information display row styles
    &.info-display .p-datatable-tbody > tr > td {
      border: none;
      padding: 0.5rem 1rem;
    }

    // Column filter element styles
    p-columnfilterformelement {
      @apply w-full max-w-full;
    }
  }
}
